import { addArticle, updateArticle, getArticle } from '@/api/article';
export default {
  middleware: 'authenticated',
  name: 'EditorIndex',
  async asyncData({params}) {
    let article = {}
    const { slug } = params
    console.log('params===>', params, slug);
    if(slug) {
      const {data} = await getArticle(slug)
      article = data.article
    }
    return {
      slug,
      article
    }
  },
  data() {
    return {
      article: {
        title: '',
        description: '',
        body: '',
        tagList: [],
      },
    }
  },
  methods: {
    async handlePublish() {
      try {
        const res = this.slug
          ? await updateArticle(this.slug, {
            article: this.article
          })
          : await addArticle({
            article: this.article
          })
        console.log('res===>', res);
        this.$router.push('/');
      } catch(e) {
        console.log('e===>', e);
        
      }
    }
  },
}
